<template>
  <el-container class="ManageSystemContainer">
    <el-header class="ManageSystemHeader"> </el-header>

    <el-aside class="ManageSystemAside">
      <h3
        v-if="ifMenuClose"
        class="ManageMenuTitle"
        @click="ManageMenuCloseClick"
      >
        目 录
      </h3>
      <el-menu
        active-text-color="#ffd04b"
        active-background="#303133"
        background-color="#606266"
        class="el-menu-vertical-demo"
        text-color="#f0f2f5"
        default-active="/Manage/StudentInformation"
        router
        :collapse="isCollapse"
      >
        <el-menu-item @click="ManageMenuCloseClick">
          <el-icon v-if="ifMenuClose" class="ManageMenuClose"><Fold /></el-icon>
          <el-icon
            v-if="!ifMenuClose"
            @click="ManageMenuOpenClick"
            class="ManageMenuOpen"
            ><Expand
          /></el-icon>
        </el-menu-item>

        <el-sub-menu index="1" @click="ManageMenuOpenClick">
          <template #title>
            <el-icon><document /></el-icon>
            <span>基本信息管理</span>
          </template>

          <el-menu-item-group>
            <el-menu-item index="/Manage/StudentInformation"
              >学生基本信息</el-menu-item
            >
            <el-menu-item index="/Manage/ClassInformation"
              >课程基本信息</el-menu-item
            >
            <el-menu-item index="/Manage/StudentGrade">学生成绩</el-menu-item>

            <el-sub-menu index="1-1">
              <template #title>
                <span>学业情况</span>
              </template>
              <!-- <el-menu-item index="/Manage/Academic">综合</el-menu-item> -->
              <el-menu-item index="/Manage/Honour">荣誉</el-menu-item>
              <el-menu-item index="/Manage/Foreign">外语</el-menu-item>
              <el-menu-item index="/Manage/Professional">职业资格</el-menu-item>
              <el-menu-item index="/Manage/Programming">编程能力</el-menu-item>
            </el-sub-menu>

            <el-menu-item index="/Manage/Competition">学科竞赛</el-menu-item>
            <el-sub-menu index="1-2">
              <template #title>
                <span>软件系统开发</span>
              </template>
              <el-menu-item index="/Manage/Project">项目能力</el-menu-item>
              <el-menu-item index="/Manage/Invention">发明专利</el-menu-item>
              <el-menu-item index="/Manage/Software">软著</el-menu-item>
            </el-sub-menu>

            <el-menu-item index="/Manage/Paper">论文发表</el-menu-item>
          </el-menu-item-group>
        </el-sub-menu>
      </el-menu>
    </el-aside>

    <el-main class="ManageSystemMain"><router-view></router-view></el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: false,
      ifMenuClose: true,
    };
  },
  mounted() {
    // 如果你想使用编程式导航
    this.$router.push("/Manage/StudentInformation");
  },
  methods: {
    setManageSystemMainStyle(width, left) {
      const ManageSystemMain = document.querySelector(".ManageSystemMain");
      ManageSystemMain.style.width = width;
      ManageSystemMain.style.left = left;
      ManageSystemMain.style.transition = "0.2s";
    },

    ManageMenuCloseClick() {
      if (this.ifMenuClose) {
        this.isCollapse = true;
        this.ifMenuClose = false;
        this.setManageSystemMainStyle("95.5%", "4.5%");
      } else {
        this.ManageMenuOpenClick();
      }
    },
    ManageMenuOpenClick() {
      this.isCollapse = false;
      this.ifMenuClose = true;
      this.setManageSystemMainStyle("82%", "18%");
    },
  },
};
</script>

<style>
@import "/src/components/ManageSystem/ManageAside.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/ManageSystemAsideMouse.css";
</style>
